@import "zero";
@import "variables";

@font-face {
  font-family: "OwnFont";
  src: url('../static/fonts/CreteRound-Regular.ttf') format("truetype");
  font-style: normal;
  font-display: swap;
}

body {
  overflow-x: hidden;
  font-family: $mainFont;
}

main {
  margin-bottom: 100px;
}

.main_container {
 @extend %main_container;
}

form {
  @include form();
  h2 {
    margin: 0 0 20px 0;
  }
  input, textarea, select {
    margin: 0 0 20px 0;
    &:focus {
      border-color: $secColor !important;
      box-shadow: 0 0 0 2px $secColor !important;
    }
  }
  .form_submit {
    @include magicButton();
  }
}

h2 {
  text-align: center;
  margin-bottom: 15px;
}

h3 {
  margin: 15px 0;
}

.basic_button {
  @include magicButton();
}

.delete_button {
  @include magicButton(red);
}

